/* Базовые стили */
* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  color: #212121;
  background-color: #fff;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

/* Стили компонента App */
.app {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 24px;
}

/* Стили компонента SearchForm */
.SearchForm_searchForm__1k24C {
  width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.SearchForm_searchInput__PLhsf {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.16), 0 0 0 1px rgba(0, 0, 0, 0.08);
  border: honeydew;
  display: block;
  width: 100%;
  padding: 16px;
  font: inherit;
  font-size: 16px;
  border-radius: 2px;
  outline: none;
}

.Modal_overlay__2EL1x {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.Modal_modal__7Vmwc {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 7px;
  max-width: 780px;
  width: 100%;
  background-color: #fff;
}

.GalleryItem_galleryItem__b8ff5 {
  position: relative;
  display: inline-block;
  overflow: hidden;
  border-radius: 2px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}
.GalleryItem_galleryItemImg__jbyrw {
  height: 260px;
  object-fit: cover;
}

.GalleryItem_stats__gIdsP {
  width: 100%;
  display: flex;
  align-items: center;
  background-color: #fff;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: #414141;
  padding: 4px;
}

.GalleryItem_statsItem__3-ghH {
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}

.GalleryItem_fullscreenButton__1tq8n {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  font: inherit;
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 50%;
  width: 56px;
  height: 56px;
  color: #fff;
  cursor: pointer;
  -webkit-transition: background-color 200ms linear, opacity 200ms linear;
  transition: background-color 200ms linear, opacity 200ms linear;
  opacity: 0;
}

.GalleryItem_fullscreenButton__1tq8n:hover,
.GalleryItem_fullscreenButton__1tq8n:focus {
  background-color: rgba(0, 0, 0, 0.7);
}

.GalleryItem_fullscreenButton__1tq8n i {
  font-size: 36px;
}

.GalleryItem_galleryItem__b8ff5:hover .GalleryItem_fullscreenButton__1tq8n,
.GalleryItem_galleryItem__b8ff5:focus .GalleryItem_fullscreenButton__1tq8n {
  opacity: 1;
}

.Gallery_gallery__WjRDe {
  width: 1120px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 16px;
  margin-left: auto;
  margin-right: auto;
}

.Loader_loader__2dO9p {
  width: 100px;
  display: block;
  margin: 0 auto;
}

/* Стили кнопки Load more */
.LoadMoreBtn_button__2c2Af {
  padding: 8px 16px;
  border-radius: 3px;
  background-color: #3884ff;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
  text-align: center;
  display: block;
  color: #fff;
  border: 0;
  text-decoration: none;
  cursor: pointer;
  font-family: inherit;
  font-size: 16px;
  line-height: 24px;
  font-style: normal;
  width: 180px;
  margin: 0 auto;
}

.LoadMoreBtn_button__2c2Af:hover,
.LoadMoreBtn_button__2c2Af:focus {
  background-color: #1f65d6;
}

